import { useState, useEffect } from 'react';

import { useSelector } from 'react-redux';

import { Link } from 'react-router-dom';

import useApiService from './../../service/ApiService';

import { likeOff, likeOn } from './../../resources/img/blog';


const BlogItem = ({id, date, title, like, likeLen}) => {

  const [nowLike, setNowLike]= useState(like)
  const [nowLenLike, setNowLenLike]= useState(likeLen)

  const { isAuthenticated, user } = useSelector(state => state);
  const {postLike} = useApiService();

  useEffect(() => {
    setNowLike(like)
  }, [like])

  const onLike = (fId, fLike) => {
    if (isAuthenticated) {
      postLike(user.id, fId, nowLike)
      if (nowLike === true) setNowLenLike(nowLenLike => nowLenLike-1);
      else setNowLenLike(nowLenLike => nowLenLike+1);
      setNowLike(nowLike => !nowLike);
    }
  }


  return (
    <div className="col-lg-3 col-md-6 mb-5">
      <div className="post-entry-1 h-100">
        <div className="post-entry-1-contents">
          <span className="meta">{date}</span>
          <h2><Link to={`/blog/detail/${id}`}>{title}</Link></h2>
          <div
            style={{ margin: '0' }}
            className="my-3 row justify-content-between align-items-center">
            <Link to={`/blog/detail/${id}`} className="more-39291">Читать</Link>
            <div
              style={{ margin: '0', height: '25px' }}
              className="row align-items-center">
              <p>{nowLenLike}</p>
              <div
                onClick={() => onLike(id, like)}
                style={{ width: '25px', height: '25px' }}
                className="img">
                <img
                  style={{ width: '100%', height: '100%', cursor: 'pointer', margin: '-27px 0 0 4px' }}
                  src={nowLike ? likeOn : likeOff}
                  alt="Лайк" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default BlogItem;