import { useEffect } from 'react';

// eslint-disable-next-line 
import { useDispatch, useSelector } from 'react-redux';
import { checkAuthenticated, loadUser } from './../../actions';

import {BrowserRouter as Router, Route, Routes, Navigate} from 'react-router-dom';

import Header from './../header/Header';
import { ActivatePage, LoginPage, ResetPasswordConfirmPage, ResetPasswordPage, SignUpPage} from './../pages/auth';
import { 
  HomePage, ContactsPage, CostTransportationPage, CostStoragePage, TrackingPage, BlogPage, 
  ProfilePage, CreateOrderPage, MyOrderPage, BlogDetailPage, NotFoundPage
} from './../pages';
import Footer from './../footer/Footer';

import './app.scss';

const App = () => {

  const { isAuthenticated } = useSelector(state => state);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(checkAuthenticated());
    dispatch(loadUser())
    // eslint-disable-next-line 
  }, []);

  return (
    <Router>
      <Header />
      <main>
        <Routes>
          <Route path="/" element={<HomePage />} />
          
          <Route path="/login" element={<LoginPage />} />
          <Route path="/signup" element={<SignUpPage />} />
          <Route path="/password/reset" element={<ResetPasswordPage />} />
          <Route path="/password/reset/confirm/:uid/:token" element={<ResetPasswordConfirmPage />} />
          <Route path="/activate/:uid/:token" element={<ActivatePage />} />

          <Route path="/create/order" element={ isAuthenticated ? <CreateOrderPage /> : <Navigate to="/login" />} />
          <Route path="/my/order" element={ isAuthenticated ? <MyOrderPage /> : <Navigate to="/login" />} />
          <Route path="/profile" element={ isAuthenticated ? <ProfilePage /> : <Navigate to="/login" />} />

          <Route path="/blog" element={<BlogPage/>} />
          <Route path="/blog/detail/:uid" element={<BlogDetailPage/>} />


          <Route path="/cost/transportation" element={<CostTransportationPage />} />
          <Route path="/cost/storage" element={<CostStoragePage/>} />
          <Route path="/tracking" element={<TrackingPage />} />
          <Route path="/contacts" element={<ContactsPage />} />

          <Route path="*" element={<NotFoundPage/>} />
        </Routes>
      </main>
      <Footer />
    </Router>
  );
};

export default App;