import { useState } from 'react';
import { Link, Route, Navigate, Routes } from 'react-router-dom';

import { useDispatch, useSelector } from 'react-redux';
import { login } from './../../../../actions';

const LoginPage = () => {

  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const { isAuthenticated } = useSelector(state => state);
  const dispatch = useDispatch();

  const onSubmit = (e) => {
    e.preventDefault();
    dispatch(login(email, password))
  }

  if (isAuthenticated) {
    return (
      <Routes>
        <Route path="*" element={<Navigate to="/profile" />} />
      </Routes>
    )
  }

  return (
    <div className="site-section">
      <div className="container">
        <div className="row mb-5 align-items-center">
          <div className="col-md-7">
            <h2 className="heading-39291 mb-0">Войти в аккаунт</h2>
          </div>
        </div>
        <form onSubmit={onSubmit} className="form">
          <div className="form-group row">
            <div className="col-lg-12 mr-auto">
              <div className="form-group row">
                <div className="col-md-5">
                  <input 
                    className="form-control"
                    type="text"
                    placeholder="Почта"
                    minLength="6"
                    required
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}/>
                </div>
              </div>
              <div className="form-group row">
                <div className="col-md-5">
                  <input 
                    className="form-control"
                    type="password"
                    placeholder="Пароль"
                    minLength="6"
                    required
                    value={password}
                    onChange={(e) => setPassword(e.target.value)} 
                    />
                </div>
              </div>
              <div className="form-group row">
                <div className="col-md-2 mr-auto">
                  <input 
                    type="submit"
                    className="btn btn-block btn-primary text-white py-2 px-2 rounded-0"
                    value="Войти" />
                </div>
              </div>
            </div>
          </div>
        </form>
        <p className="mt-3">
          У вас ещё нет аккаунта? <Link to="/signup">Регистрация</Link>
        </p>
        <p className="mt-3">
          <Link to="/password/reset">Забыли пароль?</Link>
        </p>
      </div>
    </div>
  );
}

export default LoginPage;