const useApiService = () => {
  const _baceUrl = 'http://localhost:8000/api/'

  const request = async (url, method='GET', body=null, headers={'Content-Type': 'application/json'}) => {
    try {
      const res = await fetch(url, {method, body, headers})
      if (!res.ok) {
        throw new Error(`url: ${url} \nошибка: ${res.status}`);
      }
      const data = await res.json();
      return data;

    } catch (e) {
      throw e;
    }
  }

  const _tranformContacts = (item) => {
    return {
      id: item.pk,
      address: item.address,
      phone: item.phone,
      email: item.email
    }
  }

  const getContacts = async () => {
    const res = await request(`${_baceUrl}contacts/`)
    return _tranformContacts(res[0])
  }

  const postContactsMe = async (data) => {
    return await request(`${_baceUrl}contacts-me/`, 'POST', JSON.stringify(data))
  }

  const getPriceTransportation = async (data) => {
    const res = await request(`${_baceUrl}calc/transportation/price/`, 'POST', JSON.stringify({data}))
    console.log(res);
    return res
  } 

  const getPriceStorage = async (data) => {
    const res = await request(`${_baceUrl}calc/storage/price/`, 'POST', JSON.stringify({data}))
    console.log(res);
    return res
  } 

  const postCreateOrder = async (data) => {
    return await request(`${_baceUrl}order/`, 'POST', JSON.stringify(data))
  }

  const getStatusTransportation = async (key) => {
    return await request(`${_baceUrl}status/order/get/${key}`)
  }

  const getMyOrders = async (id) => {
    return await request(`${_baceUrl}order/get/${id}`)
  }
  
  const _tranformBlogItem = (item, id) => {
    let like = false;
    item.blogUserLike.forEach((jtem) => {
      if (jtem.user == id) like = true;
    });
    return {
      id: item.id,
      title: item.title,
      date: item.date,
      like,
      likeLen: item.blogUserLike.length
    }
  }

  const getBlogItems = async (id) => {
    const res = await request(`${_baceUrl}blog/get/`)
    try {
      return res.data.map((item) => {
        return _tranformBlogItem(item, id);
      });
    } catch (err) {
      return []
    }
  }

  const postLike = async (idU, idB, like) => {
    return await request(`${_baceUrl}like/set/`, 'POST', JSON.stringify({idU, idB, like}))
  }

  const getBlogDetail = async (id) => {
    return await request(`${_baceUrl}blog/detail/${id}`)
  }

  return {
    getContacts, postContactsMe, 
    getPriceTransportation, getPriceStorage, 
    postCreateOrder, getStatusTransportation, 
    getMyOrders,
    getBlogItems, postLike, getBlogDetail}
}

export default useApiService;