import { useState } from 'react';
import { Route, Navigate, Routes, useParams } from 'react-router-dom';

import { useDispatch, useSelector } from 'react-redux';
import { resetPasswordConfirm } from './../../../../actions';

const ResetPasswordPage = () => {

  const {uid, token} = useParams();
  const [requestSent, setRequestSent] = useState(false);
  const [newPassword, setNewPassword] = useState('');
  const [reNewPassword, setNewRePassword] = useState('');

  const dispatch = useDispatch();

  const onSubmit = (e) => {
    e.preventDefault();
    dispatch(resetPasswordConfirm(uid, token, newPassword, reNewPassword));
    setRequestSent(true);
  }

  if (requestSent) {
    return (
      <Routes>
        <Route path="*" element={<Navigate to="/" />} />
      </Routes>
    )
  }

  return (
    <div className="site-section">
      <div className="container">
        <div className="row mb-5 align-items-center">
          <div className="col-md-7">
            <h2 className="heading-39291 mb-0">Установите новый пароль</h2>
          </div>
        </div>
        <form onSubmit={onSubmit} className="form">
          <div className="form-group row">
            <div className="col-lg-12 mr-auto">
              <div className="form-group row">
                <div className="col-md-5">
                  <input 
                    className="form-control"
                    type="password"
                    placeholder="Новый пароль"
                    minLength="6"
                    required
                    value={newPassword}
                    onChange={(e) => setNewPassword(e.target.value)}/>
                </div>
              </div>
              <div className="form-group row">
                <div className="col-md-5">
                  <input 
                    className="form-control"
                    type="password"
                    placeholder="Повторите новый пароль"
                    minLength="6"
                    required
                    value={reNewPassword}
                    onChange={(e) => setNewRePassword(e.target.value)}/>
                </div>
              </div>
              <div className="form-group row">
                <div className="col-md-2 mr-auto">
                  <input 
                    type="submit"
                    className="btn btn-block btn-primary text-white py-2 px-2 rounded-0"
                    value="Сброс пароля" />
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
}

export default ResetPasswordPage;