import { useState, useEffect } from 'react';

import { useSelector } from 'react-redux';
import useApiService from './../../../service/ApiService';

import './myOrderPage.scss'

const MyOrderPage = () => {
  
  const { user } = useSelector(state => state);
  const { getMyOrders } = useApiService();

  const [items, setItems] = useState([])
  const [error, setError] = useState(false)

  useEffect(() => {
    getMyOrders(user.id)
    .then((res) => {
      if (res.status !== 'bad_data') {
        console.log(res.data);
        setItems(res.data);
      }
    }).catch(() => {
      setError(true);
    })
  }, [])

  const errorMessage = error ? <span className="mt-2 col-md-8" style={{color: 'red'}}
  >Ошибка. Перезагрузите страницу или оставьте заявку для обратной свзяи</span> : null;

  return (
    <div className="site-section">
      <div className="container">
        <div className="row mb-5 align-items-center">
          <div className="col-md-7">
            <h2 className="heading-39291 mb-0">Мои заказы</h2>
          </div>
        </div>
        <ItemsOrder items={items}/>
        {errorMessage}
      </div>
    </div>
  );
};

const ItemsOrder = ({items}) => {

  const arr = items.map((item, i) => {
    return (
      <div key={i} className="order-form-item row mb-5">
        <p className="col-sm-12"><strong>Номер заказа: {item.key}</strong></p>
        <div className="order-group col-sm-4">
          <span className="mb-2">Отправитель:</span>
          <span>Cтрана: {item.from_country}</span>
          <span>Город: {item.from_city}</span>
          <span>Индекс: {item.from_zip}</span>
          <span>Адрес: {item.from_address}</span>
          <span>ФИО: {item.from_name}</span>
          <span>Телефон: {item.from_phone}</span>
        </div>
        <div className="order-group col-sm-4">
          <span className="mb-2">Получатель:</span>
          <span>Страна: {item.to_country}</span>
          <span>Город: {item.to_city}</span>
          <span>Индекс: {item.to_zip}</span>
          <span>Адрес: {item.to_address}</span>
          <span>ФИО: {item.to_name}</span>
          <span>Телефон: {item.to_phone}</span>
        </div>
        <div className="order-group col-sm-3">
          <span className="mb-2">Другие данные:</span>
          <span>Вес: {item.weight}кг</span>
          <span>Объём: {item.volume}см^3</span>
          <span>Статус: {item.status}</span>
          <span>Стоимость: {item.price}р</span>
        </div>
      </div>
    )
  })

  return (
    <form className="order-form row">
      {arr}
    </form>
  ) 
}

export default MyOrderPage;