import { useState } from 'react';
import { useSelector } from 'react-redux';
import {v4 as uuidv4} from 'uuid';

import useApiService from './../../../service/ApiService';

import './createOrderPage.scss';
import { close } from '../../../resources/img/popup';

const CreateOrderPage = () => {

  const { user } = useSelector(state => state);

  const [fromCountry, setFromCountry] = useState('');
  const [fromCity, setFromCity] = useState('');
  const [fromIndex, setFromIndex] = useState('');
  const [fromAddress, setFromAddress] = useState('');
  const [fromName, setFromName] = useState('');
  const [fromPhone, setFromPhone] = useState('');

  const [toCountry, setToCountry] = useState('');
  const [toCity, setToCity] = useState('');
  const [toIndex, setToIndex] = useState('');
  const [toAddress, setToAddress] = useState('');
  const [toName, setToName] = useState('');
  const [toPhone, setToPhone] = useState('');

  const [length, setLength] = useState('');
  const [width, setWidth] = useState('');
  const [height, setHeight] = useState('');

  const [weight, setWeight] = useState('');

  // Todo:
  const [error, setError] = useState(false);
  const [price, setPrice] = useState(false);

  const {getPriceTransportation, postCreateOrder} = useApiService();

  const onSetState = (func, value) => {
    func(value)
    setPrice(false)
  }

  const onSubmit = (e) => {
    e.preventDefault();
    if (isNaN(length) && isNaN(width) && isNaN(height) && isNaN(weight)) {
      setError(true)
      return;
    }
    setError(false);
    if (price == false) {
      setPrice(false)
      getPriceTransportation({
        fromCountry, 
        toCountry, 
        volume: Math.ceil(+length)*Math.ceil(+width)*Math.ceil(+height),
        weight: Math.ceil(+weight)
      })
        .then(res => {
          if (res.status === 'bad_data' || res.status === 'server_get_error') {
            setError(true);
          } else {
            setPrice(res.price)
          }
        })  
    } else {
      setPrice(false)
      postCreateOrder({
        'key': uuidv4(),
        'user': user.id,
        'from_country': fromCountry,
        'from_city': fromCity,
        'from_zip': fromIndex,
        'from_address': fromAddress,
        'from_name': fromName,
        'from_phone': fromPhone,
        'to_country': toCountry,
        'to_city': toCity,
        'to_zip': toIndex,
        'to_address': toAddress,
        'to_name': toName,
        'to_phone': toPhone,
        'weight': Math.ceil(+weight),
        'volume': Math.ceil(+length)*Math.ceil(+width)*Math.ceil(+height),
        'price': price,
      })
      .then(res => {
        setFromCountry('');
        setFromCity('');
        setFromIndex('');
        setFromAddress('');
        setFromName('');
        setFromPhone('');
        setToCountry('');
        setToCity('');
        setToIndex('');
        setToAddress('');
        setToName('');
        setToPhone('');
        setLength('');
        setWidth('');
        setHeight('');
        setWeight('');
      })
      .catch(() => setError(true))
    }
  }

  const onClose = () => {
    setPrice(false);
  }

  // Todo:
  const errorMessage = error ? <span 
    className="mt-2" 
    style={{color: 'red'}}>Ошибка в заполненных данных</span> : null;

  const priceMessage = (price && !error) ? <Popup
                                              onClose={onClose}
                                              price={price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")}/> : null;

  return (
    <div className="site-section">
      <div className="container">
        <div className="row mb-5 align-items-center">
          <div className="col-md-7">
            <h2 className="heading-39291 mb-0">Создать новый заказ</h2>
          </div>
        </div>
        <form onSubmit={onSubmit} className="form">
          <div className="form-group row">
            <div className="col-lg-6 mr-auto">
              <h2>Откуда забрать?</h2>
              <div className="form-group row">
                <div className="col-md-12">
                  <input 
                    type="text" 
                    className="form-control"
                    placeholder="Страна" 
                    required
                    value={fromCountry} 
                    onChange={(e) => onSetState(setFromCountry, e.target.value)}/>
                </div>
              </div>
              <div className="form-group row">
                <div className="col-md-6">
                  <input 
                    type="text" 
                    className="form-control"
                    placeholder="Город" 
                    required
                    value={fromCity}
                    onChange={(e) => onSetState(setFromCity, e.target.value)}/>
                </div>
                <div className="col-md-6">
                  <input 
                    type="text" 
                    className="form-control"
                    placeholder="Индекс" 
                    required
                    value={fromIndex}
                    onChange={(e) => onSetState(setFromIndex, e.target.value)}/>
                </div>
              </div>
              <div className="form-group row">
                <div className="col-md-12">
                  <input 
                    type="text" 
                    className="form-control"
                    placeholder="Адрес" 
                    required
                    value={fromAddress}
                    onChange={(e) => onSetState(setFromAddress, e.target.value)}/>
                </div>
                
              </div>
            </div>
            <div className="col-lg-6 ml-auto">
              <h2>Куда доставиь?</h2>
              <div className="form-group row">
                <div className="col-md-12">
                  <input 
                    type="text" 
                    className="form-control"
                    placeholder="Страна" 
                    required
                    value={toCountry} 
                    onChange={(e) => onSetState(setToCountry, e.target.value)}/>
                </div>
              </div>
              <div className="form-group row">
                <div className="col-md-6">
                  <input 
                    type="text" 
                    className="form-control"
                    placeholder="Город" 
                    required
                    value={toCity}
                    onChange={(e) => onSetState(setToCity, e.target.value)}/>
                </div>
                <div className="col-md-6">
                  <input 
                    type="text" 
                    className="form-control"
                    placeholder="Индекс" 
                    required
                    value={toIndex}
                    onChange={(e) => onSetState(setToIndex, e.target.value)}/>
                </div>
              </div>
              <div className="form-group row">
                <div className="col-md-12">
                  <input 
                    type="text" 
                    className="form-control"
                    placeholder="Адрес" 
                    required
                    value={toAddress}
                    onChange={(e) => onSetState(setToAddress, e.target.value)}/>
                </div>
                
              </div>
            </div>
          </div>
          <div className="form-group row">
            <div className="col-lg-6 mr-auto">
              <h2>Данные отправителя</h2>
              <div className="form-group row">
                <div className="col-md-12">
                  <input 
                    type="text" 
                    className="form-control"
                    placeholder="ФИО" 
                    required
                    value={fromName}
                    onChange={(e) => onSetState(setFromName, e.target.value)}/>
                </div>
              </div>
              <div className="form-group row">
                <div className="col-md-12">
                  <input 
                    type="text" 
                    className="form-control"
                    placeholder="Телефон" 
                    required
                    value={fromPhone}
                    onChange={(e) => onSetState(setFromPhone, e.target.value)}/>
                </div>
              </div>
            </div>
            <div className="col-lg-6 mr-auto">
              <h2>Данные получателя</h2>
              <div className="form-group row">
                <div className="col-md-12">
                  <input 
                    type="text" 
                    className="form-control"
                    placeholder="ФИО" 
                    required
                    value={toName}
                    onChange={(e) => onSetState(setToName, e.target.value)}/>
                </div>
              </div>
              <div className="form-group row">
                <div className="col-md-12">
                  <input 
                    type="text" 
                    className="form-control"
                    placeholder="Телефон" 
                    required
                    value={toPhone}
                    onChange={(e) => onSetState(setToPhone, e.target.value)}/>
                </div>
              </div>
            </div>
          </div>
          <div className="form-group row">
            <div className="col-lg-6 mr-auto">
              <h2>Габариты (см)</h2>
              <div className="form-group row">
                <div className="col-md-4">
                  <input 
                    type="number"
                    className="form-control"
                    placeholder="Длина" 
                    required
                    value={length}
                    onChange={(e) => onSetState(setLength, e.target.value)}/>
                </div>
                <div className="col-md-4">
                  <input 
                    type="number"
                    className="form-control"
                    placeholder="Ширина"
                    required
                    value={width}
                    onChange={(e) => onSetState(setWidth, e.target.value)}/>
                </div>
              </div>
            </div>
            <div className="col-lg-6 mr-auto">
              <h2>Вес (кг)</h2>
              <div className="form-group row">
                <div className="col-md-4">
                  <input 
                    type="number" 
                    className="form-control"
                    placeholder="Вес" 
                    required
                    value={weight}
                    onChange={(e) => onSetState(setWeight, e.target.value)}/>
                </div>
              </div>
            </div>
            <div className="col-lg-6 mr-auto">
              <div className="form-group row">
                <div className="col-md-4">
                  <input 
                    type="number"
                    className="form-control" 
                    placeholder="Высота"
                    required 
                    value={height}
                    onChange={(e) => onSetState(setHeight, e.target.value)}/>
                </div>
              </div>
            </div>
          </div>
          <div className="form-group row">
            {priceMessage ? null :              
              <div className="col-md-2 mr-2">
                <input type="submit" className="btn btn-block btn-primary text-white py-2 px-2 rounded-0" value="Создать" />
              </div>}
            {errorMessage}
            {priceMessage}
          </div>
        </form>
      </div>
    </div>
  );
};

const Popup = ({price, onClose}) => {
  return (
    <div className="popup">
      <div className="popup-wrapper">
        <span 
          onClick={onClose}
          className="popup-close ml-auto">
            <img src={close} alt="Закрыть" />
        </span>
        <div className="popup-content col-md-6">
          <p>Общая стоимось: {price}р</p>
          <div className="col-md-6 mr-auto ml-auto mt-4">
            <input type="submit" className="btn btn-block btn-primary text-white py-2 px-2 rounded-0" value="Оплатить" />
          </div>
        </div>
      </div>
    </div>
  );
};

export default CreateOrderPage;