import { useState } from 'react';

import useApiService from './../../../service/ApiService';

import './trackingPage.scss';


const TrackingPage = () => {

  const {getStatusTransportation} = useApiService();

  const [error, setError] = useState(false);
  const [success, setSuccess] =useState(false);
  const [data, setData] = useState([]);
 
  const [key, setKey] = useState('')

  const onRequest = (e) => {
    e.preventDefault();
    setError(false)
    getStatusTransportation(key)
      .then(res => {
        if (res.status != 'bad_data') {
          setSuccess(true);
          setData(res.data);
        } else {
          setError(true);
          setSuccess(false);
        }
      })
      .catch(() => {
        setError(true);
        setSuccess(false);
      })
  }

  const skeleton = success ? null : <Skeleton />;
  const errorMessage = error ? <span 
    className="mt-2 col-md-8" 
    style={{color: 'red'}}>Ошибка в заполненных данных</span> : null;
  const message = (success && data) ? <Data items={data} />: null;


  return (
    <div className="site-section">
      <div className="container">
        <div className="row mb-5 align-items-center">
          <div className="col-md-7">
            <h2 className="heading-39291 mb-0">Отслеживание груза</h2>
          </div>
        </div>
        <form onSubmit={onRequest} className="form">
          <div className="form-group row">
            <div className="col-lg-12 mr-auto">
              <h2>Введите токен заказа?</h2>
              <div className="form-group row">
                <div className="col-md-6 pl-0">
                  <div className="col-md-12">
                    <input
                      type="text"
                      className="form-control"
                      placeholder="Токен заказа"
                      value={key}
                      onChange={(e) => setKey(e.target.value)}
                    />
                  </div>
                  <div className="col-md-12 mt-4 row">
                    <div className="col-md-4">
                      <input type="submit" className="btn btn-block btn-primary text-white py-2 px-2 rounded-0" value="Отследить" />
                    </div>
                    {errorMessage}
                  </div>
                </div>

                <div className="col-md-6 pl-5 row">
                  {skeleton}
                  {message}
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

const Skeleton = () => {
  return (
    <>
      <span className="pulse skeleton-header"></span>
      <span className="pulse skeleton-block"></span>
      <span className="pulse skeleton-header"></span>
      <span className="pulse skeleton-block"></span>
    </>
  )
}

const Data = ({items}) => {
  const arr = items.map((item, i) => {
    return (
      <div key={i} className="col-md-12 mb-4">
        <span>{item.date}<br/></span>
        <span>{item.status}</span>
      </div>
    )
  })
  return (
    <>
      {arr}
    </>
  )
}

export default TrackingPage;