import { useDispatch, useSelector } from 'react-redux';
import { logoutUser } from './../../actions';

import { Link, NavLink, useLocation } from 'react-router-dom';

import './header.scss';

const Header = () => {
  const { isAuthenticated, user } = useSelector(state => state);

  const location = useLocation();

  let costPriceActive = ''; 
  let accountActive = ''; 
  
  if (location.pathname === '/cost/transportation' || location.pathname === '/cost/storage')
    costPriceActive = 'active';
    
  if (location.pathname === '/create/order' || location.pathname === '/my/order' || location.pathname === '/profile' )
    accountActive = 'active';

  const viewLinks = isAuthenticated ? <UserLinks accountActive={accountActive} /> : <GuestLinks />;

  return (
    <header className="site-navbar site-navbar-target bg-white" role="banner">
      <div className="container">
        <div className="row align-items-center position-relative">
          <div className="col-lg-4 text-center my-col-lg-1">
            <div className="site-logo">
              <Link to="/">Доставка МТК</Link>
            </div>
          </div>
          <div className="col-lg-4 my-col-lg-2">
            <nav className="site-navigation" role="navigation">
              <ul className="site-menu main-menu js-clone-nav ml-auto d-none d-lg-block">
                <li>
                  <NavLink 
                    to="/"
                    style={({ isActive }) => ({ color: isActive ? '#43ab92' : 'inherit' })} 
                    className="nav-link">Главная</NavLink></li>
                <li className="my-relative">
                  <span 
                    style={{
                      marginLeft: '15px',
                      marginRight: '15px',
                      padding: '20px 0px',
                    }}
                    className={`nav-link ${costPriceActive}`}
                    >Расчет стоимости</span>
                  <div className="my-absolute">
                    <Link to="/cost/transportation">Доставки</Link>
                    <Link to="/cost/storage">Хранения</Link>
                  </div>
                </li>
                <li>
                  <NavLink 
                    to="/tracking" 
                    className="nav-link">Отслеживание</NavLink></li>
                <li>
                  <NavLink 
                    to="/blog" 
                    className="nav-link">Блог</NavLink></li>
                <li>
                  <NavLink
                    to="/contacts" 
                    className="nav-link">Контакты</NavLink></li>
              </ul>
            </nav>
          </div>
          {viewLinks}
        </div>
      </div>
    </header>
  );
};

const UserLinks = ({accountActive}) => {

  const dispatch = useDispatch();

  return (
    <div className="col-lg-4 my-col-lg-3">
    <nav className="site-navigation text-left mr-auto " role="navigation">
      <ul className="site-menu main-menu js-clone-nav ml-auto d-none d-lg-block">
        <li className="my-relative">
          <span className={`nav-link ${accountActive}`}>Аккаунт</span>
          <div className="my-absolute">
            <Link to="/create/order">Новый заказ</Link>
            <Link to="/my/order">Мои заказы</Link>
            <Link to="/profile">Профиль</Link>
            <span></span>
            <Link
              onClick={() => dispatch(logoutUser())}
              to="/">Выйти</Link>
          </div>
        </li>
      </ul>
    </nav>
  </div>
  )
}

const GuestLinks = () => {
  return (
    <div className="col-lg-4 my-col-lg-3">
      <nav className="site-navigation text-left mr-auto " role="navigation">
        <ul className="site-menu main-menu js-clone-nav ml-auto d-none d-lg-block">
          <li>
            <NavLink 
              to="/login"
              style={({ isActive }) => ({ color: isActive ? '#43ab92' : 'inherit' })}
              className="nav-link">Войти</NavLink>
          </li>
        </ul>
      </nav>
    </div>
  )
}

export default Header;