import { useState } from 'react';
import useApiService from './../../../../service/ApiService';


const CostTransportationPage = () => {

  const [fromCountry, setFromCountry] = useState('');
  const [fromIndex, setFromIndex] = useState('');
  const [toCountry, setToCountry] = useState('');
  const [toIndex, setToIndex] = useState('');
  const [length, setLength] = useState('');
  const [width, setWidth] = useState('');
  const [height, setHeight] = useState('');
  const [weight, setWeight] = useState('');

  const [error, setError] = useState(false);
  const [price, setPrice] = useState(false);

  const {getPriceTransportation} = useApiService();

  const onSetState = (func, value) => {
    func(value)
    setPrice(false)
  }

  const onSubmit = (e) => {
    e.preventDefault();
    if (isNaN(length) && isNaN(width) && isNaN(height) && isNaN(weight)) {
      setError(true)
      return;
    }
    setError(false);
    setPrice(false)
    getPriceTransportation({
      fromCountry, 
      toCountry, 
      volume: Math.ceil(+length)*Math.ceil(+width)*Math.ceil(+height),
      weight: Math.ceil(+weight)
    })
      .then(res => {
        if (res.status === 'bad_data' || res.status === 'server_get_error') {
          setError(true);
        } else {
          // setFromCountry(''); setFromIndex('');
          // setToCountry(''); setToIndex('');
          // setLength(''); setWidth('');
          // setHeight(''); setWeight('');
          setPrice(res.price)
        }
      })  
  }

  const errorMessage = error ? <span 
                                    className="mt-2" 
                                    style={{color: 'red'}}>Ошибка в заполненных данных</span> : null;

  const priceMessage = (price && !error) ? <span 
                                    className="mt-2" 
                                    style={{color: '#43ab92'}}>= {price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")}р</span> : null;

  return (
    <div className="site-section">
      <div className="container">
        <div className="row mb-5 align-items-center">
          <div className="col-md-7">
            <h2 className="heading-39291 mb-0">Расчет стоимости доставки</h2>
          </div>
        </div>
        <form onSubmit={onSubmit} className="form">
          <div className="form-group row">
            <div className="col-lg-6 mr-auto">
              <h2>Откуда забрать?</h2>
              <div className="form-group row">
                <div className="col-md-12">
                  <input 
                    type="text" 
                    className="form-control"
                    placeholder="Страна" 
                    required
                    value={fromCountry} 
                    onChange={(e) => onSetState(setFromCountry, e.target.value)}/>
                </div>
              </div>
              <div className="form-group row">
                <div className="col-md-6">
                  <input 
                    type="text" 
                    className="form-control"
                    placeholder="Индекс" 
                    required
                    value={fromIndex}
                    onChange={(e) => onSetState(setFromIndex, e.target.value)}/>
                </div>
              </div>
            </div>
            <div className="col-lg-6 ml-auto">
              <h2>Куда доставиь?</h2>
              <div className="form-group row">
                <div className="col-md-12">
                  <input 
                    type="text" 
                    className="form-control"
                    placeholder="Страна" 
                    required
                    value={toCountry}
                    onChange={(e) => onSetState(setToCountry, e.target.value)}/>
                </div>
              </div>
              <div className="form-group row">
                <div className="col-md-6">
                  <input 
                    type="text"
                    className="form-control"
                    placeholder="Индекс" 
                    required
                    value={toIndex}
                    onChange={(e) => onSetState(setToIndex, e.target.value)}/>
                </div>
              </div>
            </div>
          </div>
          <div className="form-group row">
            <div className="col-lg-6 mr-auto">
              <h2>Габариты (см)</h2>
              <div className="form-group row">
                <div className="col-md-4">
                  <input 
                    type="number"
                    className="form-control"
                    placeholder="Длина" 
                    required
                    value={length}
                    onChange={(e) => onSetState(setLength, e.target.value)}/>
                </div>
                <div className="col-md-4">
                  <input 
                    type="number"
                    className="form-control"
                    placeholder="Ширина"
                    required
                    value={width}
                    onChange={(e) => onSetState(setWidth, e.target.value)}/>
                </div>
              </div>
            </div>
            <div className="col-lg-6 mr-auto">
              <h2>Вес (кг)</h2>
              <div className="form-group row">
                <div className="col-md-4">
                  <input 
                    type="number" 
                    className="form-control"
                    placeholder="Вес" 
                    required
                    value={weight}
                    onChange={(e) => onSetState(setWeight, e.target.value)}/>
                </div>
              </div>
            </div>
            <div className="col-lg-6 mr-auto">
              <div className="form-group row">
                <div className="col-md-4">
                  <input 
                    type="number"
                    className="form-control" 
                    placeholder="Высота"
                    required 
                    value={height}
                    onChange={(e) => onSetState(setHeight, e.target.value)}/>
                </div>
              </div>
            </div>
          </div>
          <div className="form-group row">
            <div className="col-md-2 mr-2">
              <input type="submit" className="btn btn-block btn-primary text-white py-2 px-2 rounded-0" value="Расчитать" />
            </div>
            {errorMessage}
            {priceMessage}
          </div>
        </form>
      </div>
    </div>
  );
};

export default CostTransportationPage;