import { useState } from 'react';
import { Link, Route, Navigate, Routes } from 'react-router-dom';

import { useDispatch, useSelector } from 'react-redux';
import { signUp } from './../../../../actions';

const SignUpPage = () => {

  const [accountCreated, setAccountCreated] = useState(false);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [rePassword, setRePassword] = useState('');

  const { isAuthenticated } = useSelector(state => state);
  const dispatch = useDispatch();

  const onSubmit = (e) => {
    e.preventDefault();
    if (password === rePassword) {
      dispatch(signUp(email, password, rePassword))
      setAccountCreated(true);
    }
  }

  if (isAuthenticated) {
    return (
      <Routes>
        <Route path="*" element={<Navigate to="/" />} />
      </Routes>
    )
  }

  if (accountCreated) {
    return (
      <Routes>
        <Route path="*" element={<Navigate to="/login" />} />
      </Routes>
    )
  }

  return (
    <div className="site-section">
      <div className="container">
        <div className="row mb-5 align-items-center">
          <div className="col-md-7">
            <h2 className="heading-39291 mb-0">Создайте аккаунт</h2>
          </div>
        </div>
        <form onSubmit={onSubmit} className="form">
          <div className="form-group row">
            <div className="col-lg-12 mr-auto">
              <div className="form-group row">
                <div className="col-md-5">
                  <input 
                    className="form-control"
                    type="text"
                    placeholder="Почта"
                    minLength="6"
                    required
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}/> 
                </div>
              </div>
              <div className="form-group row">
                <div className="col-md-5">
                  <input 
                    className="form-control"
                    type="password"
                    placeholder="Пароль"
                    minLength="6"
                    required
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}/> 
                </div>
              </div>
              <div className="form-group row">
                <div className="col-md-5">
                  <input 
                    className="form-control"
                    type="password"
                    placeholder="Повторите пароль"
                    minLength="6"
                    required
                    value={rePassword}
                    onChange={(e) => setRePassword(e.target.value)}/> 
                </div>
              </div>
              <div className="form-group row">
                <div className="col-md-3 mr-auto">
                  <input 
                    type="submit"
                    className="btn btn-block btn-primary text-white py-2 px-2 rounded-0"
                    value="Зарегистрироваться" />
                </div>
              </div>
            </div>
          </div>
        </form>
        <p className="mt-3">
          Уже есть аккаунт? <Link to="/login">Войти</Link>
        </p>
      </div>
    </div>
  );
}

export default SignUpPage;