import './ourAdvantages.scss';

const OurAdvantages = ({ dataOurAdvantages }) => {

  const cards = dataOurAdvantages.map((card, i) => {
    return (
      <div key={i} className="col-md-6 mb-4 col-lg-4 aos-init aos-animate" data-aos="fade-up" data-aos-delay="">
        <div className="service-29193 text-center">
          <span className="img-wrap mb-5">
            <img src={card.img} alt="advantages" className="img-fluid" />
          </span>
          <h3 className="mb-4">{card.title}</h3>
          <p>{card.text}</p>
        </div>
      </div>
    )
  })

  return (
    <div className="site-section">
      <div className="container">
        <div className="row mb-5 align-items-center">
          <div className="col-md-7">
            <h2 className="heading-39291 mb-0">Наши преимущества</h2>
          </div>
        </div>
        <div className="row">
          {cards}
        </div>
      </div>
    </div>
  );
};

export default OurAdvantages;