import { 
  loginSuccess,
  loginFail,

  signUpSuccess,
  signUpFail,
  activationSuccess,
  activationFail,

  userLoadedSuccess,
  userLoadedFail,

  authenticatedSuccess,
  authenticatedFail,

  passwordResetSuccess,
  passwordResetFail,
  passwordResetConfirmSuccess,
  passwordResetConfirmFail,

  logout
} from './auth';

import axios from 'axios';


const _apiUrl = "http://localhost:8000";

export const loadUser = () => async dispatch => {
  const token = localStorage.getItem('access');
  if (token) {
    const url = `${_apiUrl}/auth/users/me/`;
    const config = {
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `JWT ${token}`,
        'Accept': 'application/json'
      }
    };

    try {
        const res = await axios.get(url, config)
        dispatch(userLoadedSuccess(res.data));
      } catch(err) {
        dispatch(userLoadedFail());
      }

  } else {
    dispatch(userLoadedFail());
  }
}

export const login = (email, password) => async dispatch => {
  const url = `${_apiUrl}/auth/jwt/create/`;
  const config = {headers: {'Content-Type': 'application/json'}};
  const body = JSON.stringify({email, password});

  try {
    const res = await axios.post(url, body, config)
    dispatch(loginSuccess(res.data));
    dispatch(loadUser())
  } catch(err) {
    dispatch(loginFail());
  }
}

export const signUp = (email, password, re_password) => async dispatch => {
  const url = `${_apiUrl}/auth/users/`;
  const config = {headers: {'Content-Type': 'application/json'}};
  const body = JSON.stringify({email, password, re_password});

  try {
    await axios.post(url, body, config)
    dispatch(signUpSuccess());
  } catch(err) {
    dispatch(signUpFail());
  }
}

export const verifySignUp = (uid, token) => async dispatch => {
  const url = `${_apiUrl}/auth/users/activation/`;
  const config = {headers: {'Content-Type': 'application/json'}};
  const body = JSON.stringify({uid, token});

  try {
    await axios.post(url, body, config)
    dispatch(activationSuccess());
  } catch(err) {
    dispatch(activationFail());
  }
} 

export const checkAuthenticated = () => async dispatch => {
  const token = localStorage.getItem('access');
  if (token) {
    const url = `${_apiUrl}/auth/jwt/verify/`;
    const config = {
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    };
    const body = JSON.stringify({token});
    try {
      const res = await axios.post(url, body, config)
      if (res.data.code !== 'token_not_valid') {
        dispatch(authenticatedSuccess())
      } else {
        dispatch(authenticatedFail())
      }
    } catch(err) {
      dispatch(authenticatedFail())
    }

  } else {
    dispatch(authenticatedFail())
  }
} 


export const resetPassword = (email) => async dispatch => {
  const url = `${_apiUrl}/auth/users/reset_password/`;
  const config = {
    headers: {
      'Content-Type': 'application/json',
    }
  };
  const body = JSON.stringify({email});

  try {
    await axios.post(url, body, config)
    dispatch(passwordResetSuccess())
  } catch(err) {
    dispatch(passwordResetFail())
  }

}

export const resetPasswordConfirm = (uid, token, new_password, re_new_password) => async dispatch => {
  const url = `${_apiUrl}/auth/users/reset_password_confirm/`;
  const config = {
    headers: {
      'Content-Type': 'application/json',
    }
  };
  const body = JSON.stringify({uid, token, new_password, re_new_password});

  try {
    await axios.post(url, body, config)
    dispatch(passwordResetConfirmSuccess())
  } catch(err) {
    dispatch(passwordResetConfirmFail())
  }
}

export const logoutUser = () => async dispatch => {
  dispatch(logout())
}