import { useState, useEffect } from 'react';

import useApiService from './../../../service/ApiService';

const ContactsPage = () => {

  const [contactsInfo, setContactInfo] = useState({
    id: '',
    address: '',
    phone: '',
    email: '',
  })

  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');

  const [errorData, setErrorData] = useState(false);

  const {getContacts, postContactsMe} = useApiService();

  useEffect(() => {
    onRequest()
    setErrorData(false);
  }, [])

  const onRequest = () => {
    getContacts()
      .then(data => {
        setContactInfo(data)
      });
  }

  const onSendRequest = (e) => {
    e.preventDefault();
    setErrorData(false);
    postContactsMe({
      name: name,
      email: email,
      notes: message
    })
      .then(res => {
        if (res.status === 'bad_data') {
          setErrorData(true);
        } else {
          setName('');
          setEmail('');
          setMessage('');
        }
      })  
  }

  const errorMessage = errorData ? <span 
                                    className="mt-2" 
                                    style={{color: 'red'}}>Ошибка в заполненных данных</span> : null;

  return (
    <div className="site-section">
      <div className="container">
      <div className="row mb-5 align-items-center">
          <div className="col-md-7">
            <h2 className="heading-39291 mb-0">Свяжитесь с нами</h2>
          </div>
        </div>
        <div className="row">
          <div className="col-lg-8 mb-5">
            <form onSubmit={onSendRequest}>
              <div className="form-group row">
                <div className="col-md-12">
                  <input 
                    type="text" 
                    className="form-control"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    placeholder="Имя" />
                </div>
              </div>
              <div className="form-group row">
                <div className="col-md-12">
                  <input 
                    type="text"
                    className="form-control"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    placeholder="Почта" />
                </div>
              </div>
              <div className="form-group row">
                <div className="col-md-12">
                  <textarea
                    className="form-control"
                    placeholder="Напишите своё сообщение"
                    value={message}
                    onChange={(e) => setMessage(e.target.value)}
                    cols="30"
                    rows="10">
                  </textarea>
                </div>
              </div>
              <div className="form-group row">
                <div className="col-md-6 mr-2">
                  <input
                    type="submit"
                    className="btn btn-block btn-primary text-white py-2 px-4 rounded-0"
                    value="Отправить сообщение" />
                </div>
                {errorMessage}
              </div>
            </form>
          </div>
          <div className="col-lg-4 ml-auto">
            <div className="bg-white p-3 p-md-5">
              <h3 className="heading-39291">Контактная информация</h3>
              <ul className="list-unstyled footer-link">
                <li className="d-block mb-3">
                  <span className="d-block text-black small text-uppercase font-weight-bold">Адрес:</span>
                  <span>{contactsInfo.address}</span></li>
                <li className="d-block mb-3">
                  <span className="d-block text-black small text-uppercase font-weight-bold">Телефон:</span>
                  <span>{contactsInfo.phone}</span>
                </li>
                <li className="d-block mb-3">
                  <span className="d-block text-black small text-uppercase font-weight-bold">Почта:</span>
                  <span>{contactsInfo.email}</span>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ContactsPage;