import { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';

import useApiService from './../../../service/ApiService';

import BlogList from './../../blogList/BlogList';


const BlogPage = () => {

  const { isAuthenticated, user } = useSelector(state => state);
  const {getBlogItems} = useApiService();

  const [items, setItems] = useState([]);

  useEffect(() => {
    const usId = isAuthenticated ? user.id : null;
    getBlogItems(usId)
      .then((res) => {
        setItems(res);
      })
  }, [user])

  // const qPages = 5;

  return (
    <>
      <BlogList dataBlogItems={items} qPages={0}/>
    </>
  );
};

export default BlogPage;