import { useMemo } from 'react';

import { Link } from 'react-router-dom';

import BlogItem from './../blogItem/BlogItem';

import './blogList.scss'

const BlogList = ({ dataBlogItems, qPages }) => {

  const cards = dataBlogItems.map(data => {
    return (
      <BlogItem 
        key={data.id}
        id={data.id} 
        date={data.date} 
        title={data.title} 
        like={data.like} 
        likeLen={data.likeLen} />
    )
  })

  const renderLinkPages = (qPages) => {
    let arr = [];
    for (let i = 1; i <= qPages; i++) { 
      arr[i-1] = <Link key={i} className="col-lg-2" to={`/blog`}>{i}</Link>
    }
    return (
      <div className="row paginations col-lg-3">
        {arr}
      </div>
    )
  }

  const linkPages =  useMemo(() => renderLinkPages(qPages), [qPages])

  return (
    <div className="site-section">
      <div className="container">
        <div className="row  mb-5">
          <div className="col-md-7">
            <h2 className="heading-39291">БЛОГ И ОБНОВЛЕНИЯ</h2>
            <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Iusto quos veniam magni totam, architecto earum dolor id obcaecati!</p>
          </div>
        </div>
        <div className="row align-items-stretch">
          {cards}
        </div>
        {linkPages}
      </div>
    </div>
  );
};

export default BlogList;