import { useState } from 'react';
import { Link, Route, Navigate, Routes } from 'react-router-dom';

import { useDispatch } from 'react-redux';
import { resetPassword } from './../../../../actions';

const ResetPasswordPage = () => {

  const [request, setRequest] = useState(false);
  const [email, setEmail] = useState('');

  const dispatch = useDispatch();

  const onSubmit = (e) => {
    e.preventDefault();
    dispatch(resetPassword(email));
    setRequest(true);
  }

  if (request) {
    return (
      <Routes>
        <Route path="*" element={<Navigate to="/" />} />
      </Routes>
    )
  }

  return (
    <div className="site-section"> 
      <div className="container">
        <div className="row mb-5 align-items-center">
          <div className="col-md-7">
            <h2 className="heading-39291 mb-0">Востановите пароль</h2>
          </div>
        </div>
        <form onSubmit={onSubmit} className="form">
          <div className="form-group row">
            <div className="col-lg-12 mr-auto">
              <div className="form-group row">
                <div className="col-md-5">
                  <input 
                    className="form-control"
                    type="text"
                    placeholder="Почта"
                    minLength="6"
                    required
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}/>
                </div>
              </div>
              <div className="form-group row">
                <div className="col-md-2 mr-auto">
                  <input 
                    type="submit"
                    className="btn btn-block btn-primary text-white py-2 px-2 rounded-0"
                    value="Востановить" />
                </div>
              </div>
            </div>
          </div>
        </form>
        <p className="mt-3">
          Уже есть аккаунт? <Link to="/login">Войти</Link>
        </p>
        <p className="mt-3">
          У вас ещё нет аккаунта? <Link to="/signup">Регистрация</Link>
        </p>
      </div>
    </div>
  );
}

export default ResetPasswordPage;