import { useState } from 'react';

import { Link } from 'react-router-dom';

import useApiService from './../../service/ApiService';

import './footer.scss';


const Footer = () => {
  const [email, setEmail] = useState('');
  const [errorData, setErrorData] = useState(false);
  
  const {postContactsMe} = useApiService();

  const onSendRequest = (e) => {
    e.preventDefault();
    setErrorData(false);
    postContactsMe({
      email: email,
    })
      .then(res => {
        if (res.status === 'bad_data') {
          setErrorData(true);
        } else {
          setEmail('');
        }
      })  
  }

  const errorMessage = errorData ? <span 
                                    className="mt-2" 
                                    style={{color: 'red'}}>Ошибка в заполненных данных</span> : null;

  return (
    <footer className="site-footer">
      <div className="container">
        <div className="row">
          <div className="col-md-8">
            <div className="row">
              <div className="col-md-5">
                <h2 className="footer-heading mb-4">О компании</h2>
                <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Quos maiores repellat iusto nam. Voluptates consequuntur molestias corrupti cupiditate inventore! Veritatis.</p>
              </div>
              <div className="col-md-6 ml-auto">
                <h2 className="footer-heading mb-4">Ресурсы</h2>
                <ul className="list-unstyled">
                  <li><Link to="/">Главная</Link></li>
                  <li><Link to="/cost/transportation">Расчет стоимости доставки</Link></li>
                  <li><Link to="/cost/storage">Расчет стоимости хранения</Link></li>
                  <li><Link to="/tracking">Отслеживание</Link></li>
                  <li><Link to="/blog">Блог</Link></li>
                  <li><Link to="/contacts">Контакты</Link></li>
                </ul>
              </div>
            </div>
          </div>
          <div className="col-md-4 ml-auto">
            <div className="mb-5">
              <h2 className="footer-heading mb-4">Свяжитесь со мной</h2>
              <form onSubmit={onSendRequest} className="footer-suscribe-form">
                <div className="input-group mb-3">
                  <input 
                    className="form-control rounded-0 border-secondary text-white bg-transparent" 
                    type="text" 
                    placeholder="Введитите почту"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)} />
                    <div className="input-group-append">
                      <button 
                        className="btn btn-primary text-white"
                        type="submit" 
                        id="button-addon2">Отправить</button>
                    </div>
                </div>
                {errorMessage}
              </form>
            </div>
            <h2 className="footer-heading mb-4">Подпишитесь на нас</h2>
            <Link to="/" className="smoothscroll pl-0 pr-3"><span className="icon-facebook"></span></Link>
            <Link to="/" className="pl-3 pr-3"><span className="icon-twitter"></span></Link>
            <Link to="/" className="pl-3 pr-3"><span className="icon-instagram"></span></Link>
            <Link to="/" className="pl-3 pr-3"><span className="icon-linkedin"></span></Link>
          </div>
        </div>
      </div>
    </footer>
  );
};

export default Footer;