import { useState } from 'react';

import { Swiper, SwiperSlide } from 'swiper/react';
import { Thumbs, Pagination } from 'swiper';
import 'swiper/css';
import 'swiper/css/pagination';

import './slider.scss';

const Slider = ({dataSliders}) => {

  const [thumbsSwiper, setThumbsSwiper] = useState(null);

  const slidesBig = dataSliders.map((slide, i) => {
    return <SwiperSlide key={i}><img src={slide.img} alt="Imag" /></SwiperSlide>
  }); 

  const slidesSmall = dataSliders.map((slide, i) => {
    return (
      <SwiperSlide key={i}>
        <h1 className="slider-smoll__title">{slide.title}</h1>
        <p className="slider-smoll__text">{slide.text}</p>
      </SwiperSlide>
    )
  }); 

  return (
    <section className="slider">
      <div className="slider-big">
        <Swiper
          spaceBetween={0}
          slidesPerView={1}
          modules={[Thumbs, Pagination]}
          pagination={{ clickable: true }}
          watchSlidesProgress
          thumbs={{ swiper: thumbsSwiper }}
          speed={1200}
          longSwipesRatio={0}
          >
            {slidesBig}
        </Swiper>
      </div>
      <div className="slider-smoll">
        <Swiper
          spaceBetween={0}
          slidesPerView={1}
          modules={[Thumbs]}
          watchSlidesProgress
          onSwiper={setThumbsSwiper}
          speed={1100}
        >
          {/* <SwiperSlide>
            <h1 className="slider-smoll__title">Lorem ipsum dolor sit amet consectetur adipisicing.</h1>
            <p className="slider-smoll__text">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Accusamus harum, error minima?</p>
          </SwiperSlide>
          <SwiperSlide>
            <h1 className="slider-smoll__title">Lorem ipsum dolor sit amet consectetur adipisicing.</h1>
            <p className="slider-smoll__text">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Accusamus harum, error minima?</p>
          </SwiperSlide>
          <SwiperSlide>
            <h1 className="slider-smoll__title">Lorem ipsum dolor sit amet consectetur adipisicing.</h1>
            <p className="slider-smoll__text">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Accusamus harum, error minima?</p>
          </SwiperSlide> */}
          {slidesSmall}
        </Swiper>
      </div>
    </section>
    
  );
};

export default Slider;