import { useState } from 'react';
import { Route, Navigate, Routes, useParams } from 'react-router-dom';

import { useDispatch, useSelector } from 'react-redux';
import { verifySignUp } from './../../../../actions';

const ActivatePage = () => {

  const {uid, token} = useParams();
  const [verify, setVerify] = useState(false);

  const dispatch = useDispatch();

  const verifyAccount = e => {
    e.preventDefault();
    console.log(uid, token);
    dispatch(verifySignUp(uid, token));
    setVerify(true);
  }

  if (verify) {
    return (
      <Routes>
        <Route path="*" element={<Navigate to="/" />} />
      </Routes>
    )
  }

  return (
    <div className="site-section">
      <div className="container">
        <div className="row mb-5 align-items-center">
          <div className="col-md-7">
            <h2 className="heading-39291 mb-0">Подтвердите свою учётную запись</h2>
          </div>
        </div>
        <button
          className="btn btn-primary"
          onClick={verifyAccount}>Подтвердить</button>
      </div>
    </div>
  );
}

export default ActivatePage;